// HOME
.section {
  display: flex;
  flex-direction: column;
  overflow: hidden;
 }  // &--main

@include r($sm) {
  .section {
    display: none;
    height: auto !important;
    .fp-tableCell {
      height: auto !important; }
    &--main {
      display: flex; } } }

// AOS FIX
.section.active {
  [data-aos] {
    transition-delay: .3s !important;
    opacity: 1 !important;
    transform: none !important;
    pointer-events: auto !important; } }


/* home section */
.home-section {
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
  position: relative;
  min-height: 100vh;
  background: black;
  color: white;
  overflow: hidden;
  &__background {
    position: absolute 0;
    z-index: 1;
    display: flex;
    flex-direction: column;
    font-size: 0;
    pointer-events: none;
    img, picture, video {
      position: relative;
      z-index: 1;
      flex: 1 1 auto;
      height: 100%;
      width: 100%;
      object-fit: cover; }
    video + img, picture {
      display: none; }
    &--with-overlay {
      &::after {
        display: inline-block;
        content: ' ';
        position: absolute 0;
        z-index: 2;
        background: #202020;
        opacity: 0.4;
        z-index: 2;
        will-change: transform;
        backface-visibility: hidden; } } }

  &__background-overlay {
    flex: 1 1 auto;
    width: 100%;
    pointer-events: none;
    will-change: transform;
    backface-visibility: hidden;
    &::after, &::before {
      position: absolute;
      left: -20%;
      right: -20%;
      height: 450px;
      display: inline-block;
      content: ' ';
      z-index: 3;
      background: #202020;
      opacity: 0.4;
      filter: blur(150px); }
    &::after {
      top: -280px; }
    &::before {
      bottom: -280px; } }


  &__content {
    flex: 1 1 auto;
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 1440px;
    width: 100%;
    padding-left: 150px;
    padding-top: 80px;
    margin-left: auto;
    will-change: transform;
    backface-visibility: hidden;
    transform-style: preserve-3d; }

  &__title {
    margin: auto 0 0;
    padding-right: 20px;
    font-weight: 800;
    font-size: 110px;
    line-height: 1; }

  &__description {
    padding-right: 20px;
    font-weight: 600;
    font-size: 22px;
    line-height: 1.5;
    margin-top: 1em;
    max-width: 830px; }

  &__cta {
    margin: 2em 0 -1em;
    a {
      display: flex;
      align-items: center;
      &:hover {
        .home-section__cta-icon {
          transform: translate(7px,0); } } }
    span {
      display: inline-block;
      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
      margin-right: 14px; } }

  &__cta-icon {
    size: 48px;
    background: $colorPrimary;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 0;
    border-radius: 50%;
    transition: transform .25s $ease;
    backface-visibility: hidden;
    will-change: transform;
    transform-style: preserve-3d;
    .ico {
      font-size: 12px; } }

  &__bottom {
    width: 100%;
    margin-top: 90px; }

  &__news {
    border-top: 1px solid rgba(white, .3); }

  &__navmenu {
    background: white;
    color: $fontColor; } }

// IE FIX
.is-ie {
  @include r($sm) {
    .section {
      flex: 0 0 auto;
      display: block; } }
  .home-section {
    &__background-overlay {
      display: none; }
    &__background {
      video {
        display: none;
        & + img, picture {
          display: block; } } }
    &__content {
      height: 100vh;
      @include r($sm) {
        height: auto; } }
    &__cta-icon {
      background-image: svg-load('../icons/svg-mono/btn-arrow-next.svg');
      background-repeat: no-repeat;
      background-position: center center; } } }

@include r($hd) {
  .home-section {
    &__title {
      font-size: 85px; }

    &__description {
      font-size: 18px;
      line-height: 1.67; }

    &__bottom {
      margin-top: 70px; } } }

@include r($xl) {
  .home-section {
    &__content {
      padding-left: 40px; } } }

@include r($lg) {
  .home-section {
    &__title {
      font-size: 60px; }
    &__description {
      font-size: 16px;
      max-width: 620px; } } }

@include r($md) {
  .home-section {
    &__title {
      font-size: 38px; } } }

@media only screen and (max-height: 600px) {
  .home-section {
    &__cta {
      margin-top: 1em; }
    &__bottom {
      margin-top: 40px; } } }

@include r($sm) {
  .home-section {
    min-height: 375px !important;
    &__background-overlay {
      display: none; }
    &__title {
      font-size: 28px; }
    &__content {
      padding: 35px 20px 25px; }
    &__description {
      font-size: 14px;
      max-width: 300px; }
    &__cta {
      margin: 1em 0 0;
      span {
        font-size: 14px;
        margin-right: 8px; } }
    &__cta-icon {
      size: 32px;
      .ico {
        font-size: 10px; } }
    &__bottom {
      margin-top: 50px; }
    &--main {
      // min-height: 100vh
      min-height: 1px;
      background: white;
      padding-top: 48px;
      &::after {
        display: inline-block;
        content: ' ';
        position: absolute;
        bottom: 0;
        z-index: 2;
        left: 15px;
        right: 15px;
        height: 1px;
        background: $borderColor; }
      .home-section {
        &__background {
          flex: 1 1 auto;
          position: relative auto;
          min-height: 280px; }
        &__background-overlay {
          position: absolute 0; }
        &__content {
          flex: 0 0 auto;
          padding: 0 0 0 15px;
          margin-top: -80px; } } } } }


@include r(414) {
  .home-section {
    &__title {
      font-size: 24px; }
    &__description {
      font-size: 11px; } } }
