// Styles for dialog window
.mfp-wrap {
  -webkit-overflow-scrolling: touch;
  -webkit-transform: translateZ(0); }

html.mfp-helper {
  height: 100%;
  body {
    overflow: hidden;
    height: 100%;
    -webkit-transform: translateZ(0); } }

// Fade-zoom animation
.mfp-zoom-in {
  .modal {
    opacity: 0;
    transition: all 0.2s ease-in-out;
    transform: scale(0.8); }
  &.mfp-ready .modal {
    opacity: 1;
    transform: scale(1); }
  &.mfp-removing .modal {
    transform: scale(0.8);
    opacity: 0; }
  &.mfp-bg {
    background: rgba($fontColor, .7);
    opacity: 0;
    transition: opacity 0.3s $ease; }
  &.mfp-ready.mfp-bg {
    opacity: 1; }
  &.mfp-removing.mfp-bg {
    opacity: 0; } }


// Fade-move animation
.my-mfp-slide-bottom {
  .zoom-anim-dialog {
    opacity: 0;
    transition: all 0.2s ease-out;
    transform: translateY(-20px) perspective(600px) rotateX(10deg); }
  &.mfp-ready .zoom-anim-dialog {
    opacity: 1;
    transform: translateY(0) perspective(600px) rotateX(0); }
  &.mfp-removing .zoom-anim-dialog {
    opacity: 0;
    transform: translateY(-10px) perspective(600px) rotateX(10deg); }
  &.mfp-bg {
    opacity: 0;
    transition: opacity 0.3s ease-out; }
  &.mfp-ready.mfp-bg {
    opacity: 0.8; }
  &.mfp-removing.mfp-bg {
    opacity: 0; } }
