.col-border-left {
  padding-left: 45px;
  &::before {
    display: inline-block;
    content: ' ';
    position: absolute;
    left: 15px;
    top: 1em;
    bottom: 0;
    width: 1px;
    background: $borderColor; } }

.row-border-bottom {
  position: relative;
  > [class*="col"] {
    padding-bottom: 70px; }
  &::after {
    display: inline-block;
    content: ' ';
    position: absolute;
    left: 15px;
    right: 15px;
    bottom: 0;
    height: 1px;
    background: $borderColor; } }

@include r($hd) {
  .row {
    &--production {
      max-width: 880px;
      margin: 0 auto;
      &::after {
        display: none; } } } }

@include r($xl) {
  .row-border-bottom {
    > [class*="col"] {
      padding-bottom: 20px; }
    &::after {
      display: none; } }
  .row {
    &--production {
      max-width: 710px; } } }

@include r($lg) {
  .col-border-left {
    padding-left: 15px;
    &::before {
      display: none; } }
  .row-border-bottom {
    > [class*="col"] {
      padding-bottom: 0px; } }

  .row {
    &--production {
      max-width: 100%;
      > [class*="col"] {
        padding-left: 0;
        padding-right: 0; } } } }
