/* HEADER */
.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 9;
  background: transparent;
  will-change: transform;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  pointer-events: none;
  &__logo, &__menu, &__cta, &__hamburger {
    pointer-events: all; }

  &__wrapper {
    color: white;
    display: flex;
    align-items: center;
    padding: 0px 0 0 65px; }

  &__logo {
    flex: 0 0 auto;
    margin-right: 40px;
    transition: transform .25s $ease;
    will-change: transform;
    svg {
      width: auto;
      height: 40px;
      width: 121px; }
    &:hover {
      transform: scale(1.03); } }

  &__menu {
    flex: 0 1 auto;
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
    font-family: 'Helvetica';
    li {
      display: inline-block;
      margin-right: 30px;
      text-transform: uppercase;
      font-size: 14px;
      letter-spacing: 2px;
      &.is-active a {
        &::after {
          right: 5px; } } }
    a {
      position: relative;
      padding: 5px;
      transition: color .25s $ease;
      &::after {
        display: block;
        content: ' ';
        position: absolute;
        bottom: 0;
        left: 5px;
        right: 100%;
        background: $colorOrange;
        height: 2px;
        transition: right .25s $ease; }
      &:hover {
        &::after {
          right: 5px; } } } }

  &__menu-counter {
    position: absolute;
    top: 0;
    right: -5px;
    font-size: 10px;
    line-height: 1;
    letter-spacing: 2px; }

  &__cta {
    flex: 0 0 auto;
    display: flex;
    align-items: center;
    margin-left: auto;
    margin-right: 40px; }

  &__phone {
    flex: 0 0 auto;
    padding: 10px;
    margin-right: 50px;
    display: flex;
    align-items: center;
    font-weight: 600;
    font-size: 14px;
    letter-spacing: 2px;
    text-transform: uppercase;
    transition: color .25s $ease;
    .ico {
      font-size: 10px; }
    span {
      margin-left: 7px; }
    &:hover {
      color: rgba(white, .7); } }

  &__search, &__callback, &__mail {
    padding: 10px;
    margin-right: 30px;
    font-size: 0; }

  &__callback {
    display: none;
    .ico {
      font-size: 18px; } }
  &__mail {
    display: none;
    .ico {
      font-size: 14px; } }
  &__search {
    .ico {
      font-size: 18px; } }

  &__lang {
    padding: 10px;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 2px;
    text-transform: uppercase; }

  &__hamburger {
    .hamburger-inner {
      background-color: white;
      &::before, &::after {
        background-color: white; } } }

  // animations and js part
  &__logo, &__menu, &__cta {
    transition: transform .25s $ease, opacity .25s $ease; }

  &.is-fixed {
    // should be normalized to -100% scroll at this point
    position: fixed;
    transform: translate3d(0,-100%,0) !important;
    transition: transform .25s $ease; }

  &.is-fixed-visible {
    transform: translate3d(0,0,0) !important; }

  &.is-transitioning {
    transition: transform .25s $ease;
    transform: translate3d(0,-100%,0) !important; }

  &.is-menu-active {
    pointer-events: none;
    .header {
      &__logo, &__menu, &__cta {
        transform: translate3d(0,-30px, 0);
        opacity: 0;
        pointer-events: none; }
      &__hamburger {
        pointer-events: all; } } }

  &[data-modifier="is-black"] {
    .header {
      &__wrapper {
        color: $fontColor; }
      &__logo {
        .svg-logo-letters {
          fill: $fontColor; } }
      &__phone {
        &:hover {
          color: $colorPrimary; } } }

    // &.is-fixed
    //   background: white
    //   box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1)
    //   .hamburger
    //     size: 80px
    //   &.is-menu-active
    //     background: transparent
 }    //     box-shadow: none


  &.is-fixed {
    background: white;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
    .hamburger {
      size: 80px; }
    &.is-menu-active {
      background: transparent;
      box-shadow: none; }
    .header {
      &__wrapper {
        color: $fontColor; }
      &__logo {
        .svg-logo-letters {
          fill: $fontColor; } }
      &__phone {
        &:hover {
          color: $colorPrimary; } } } } }


@include r($hd) {
  .header {
    &__wrapper {
      padding-left: 50px; }
    &__logo {
      margin-right: 50px; }
    &__menu {
      li {
        font-size: 12px;
        margin-right: 20px; } }
    &__cta {
      margin-right: 50px; }
    &__phone {
      font-size: 12px;
      margin-right: 35px; } } }

// 1280 and below
@include r($xl) {
  .header {
    &__wrapper {
      padding-left: 40px; }
    &__logo {
      margin-right: 20px; }
    &__menu {
      display: none; }
    &__cta {
      margin-right: 60px; }
    &__callback, &__mail {
      display: block; }
    &__search, &__callback, &__mail {
      margin-right: 20px; } } }

// tablet
@include r($lg) {
  .header {
    &__logo {
      svg {
        height: 32px;
        width: 97px; } }
    &__cta {
      margin-right: 30px; }

    &.is-menu-active {
      .header {
        &__logo {
          transform: translate3d(0,0px, 0);
          opacity: 1;
          pointer-events: all; } } } } }

@include r($md) {
  .header {
    &__phone {
      display: none; } } }

// mobile
@include r($sm) {
  .header {
    background: white;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
    &__wrapper {
      padding-left: 20px;
      color: $fontColor; }

    &__logo {
      svg {
        height: 24px;
        width: 73px; }
      .svg-logo-letters {
        fill: $fontColor; } }

    &__cta {
      margin-right: 10px; }

    &.is-menu-active {
      pointer-events: all;
      .header {
        &__logo, &__menu, &__cta {
          transform: translate3d(0,0px, 0);
          opacity: 1;
          pointer-events: all; } } }

    &.is-fixed {
      .hamburger {
        size: 48px; }
      &.is-menu-active {
        background: white;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1); } } } }

@include r(414) {
  .header {
    &__mail {
      display: none; }
    &__search, &__callback {
      margin-right: 10px; } } }


// Header navigation
.header-nav {
  position: fixed;
  z-index: 7;
  top: 0;
  bottom: 0;
  height: 100vh;
  left: 65px;
  display: flex;
  flex-direction: column;
  color: white;
  pointer-events: none;
  will-change: transform;
  backface-visibility: hidden;
  transition: transform .25s $ease, opacity .25s $ease;
  &__wrapper {
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
    overflow: visible;
    padding: 100px 0 20px; }
  &__actions {
    display: flex;
    flex-direction: column; }
  &__action {
    position: relative;
    display: inline-block;
    text-align: center;
    padding: 10px;
    margin-bottom: 15px;
    font-size: 0;
    pointer-events: all;
    transition: color .25s $ease;
    &:last-child {
      margin-bottom: 0; }
    .ico {
      font-size: 1rem;
      &-mono-mail {
        font-size: 14px; }
      &-mono-phone-outline {
        font-size: 18px; }
      &-mono-arrow-top {
        font-size: 19px; } } }
  &[data-modifier="is-black"] {
    color: $fontColor; }
  &.is-fixed {
    transform: translate3d(0,-20px,0);
    opacity: 0;
    .header-nav {
      &__action {
        pointer-events: none; } } }
  &.is-fixed-visible {
    transform: translate3d(0,0,0);
    opacity: 1;
    .header-nav {
      &__action {
        pointer-events: all; } } } }

@include r($hd) {
  .header-nav {
    left: 50px;
    &__wrapper {
      padding-top: 85px; } } }

@include r($xl) {
  .header-nav {
    display: none; } }
