.about {
  background: #020102; }

// about
.about-hero {
  position: relative;
  &__background {
    position: absolute 0;
    display: flex;
    flex-direction: column;
    z-index: 1;
    img {
      flex: 1 1 auto;
      width: 100%;
      height: 100%;
      object-fit: cover; }
    &::after {
      display: block;
      position: absolute;
      content: '';
      bottom: 0;
      left: 0;
      width: 100%;
      height: 200px;
      background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,1) 100%); } }

  &__content {
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    position: relative;
    z-index: 3;
    color: white;
    min-height: 100vh;
    padding: 140px 0 200px; }

  &__title {
    max-width: 710px;
    font-weight: 700;
    font-size: 36px;
    line-height: 1.22; }

  &::after {
    display: inline-block;
    content: ' ';
    position: absolute;
    z-index: 2;
    height: 700px;
    bottom: -350px;
    left: -20%;
    right: -20%;
    background: #020102;
    filter: blur(250px);
 } }    // background: rgba(red, .2)


// about content
.about-content {
  position: relative;
  z-index: 2;
  margin-top: -157px;
  // background: #020102
  color: white;
  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
    padding-bottom: 75px;
    position: relative;
    &:before {
      position: absolute;
      display: block;
      content: '';
      width: calc(100% - 30px);
      height: 2px;
      background: $fontColor;
      left: 15px;
      bottom: 0; } }
  &__text, &__sidebar {
    width: 100%;
    min-width: 1px;
    min-height: 0; }
  &__text {
    flex: 0 0 52%;
    max-width: 52%;
    padding-left: 15px;
    padding-right: 30px; }
  &__sidebar {
    flex: 0 0 40%;
    max-width: 40%;
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto; }
  .wysiwyg {
    p {
      font-size: 18px;
      color: white; } } }

// about navi
.about-navi {
  background: #020102;
  padding: 170px;
  color: white;
  &__heading {
    text-align: center;
    font-weight: 600;
    font-size: 18px;
    line-height: 1.22; }

  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
    text-align: center;
    li {
      display: block;
      margin: 5px -10px 15px; }
    a {
      display: inline-block;
      position: relative;
      padding: 10px;
      font-weight: 700;
      font-size: 40px;
      line-height: 1.25;
      text-align: center;
      transition: color .25s $ease;
      &::after {
        display: inline-block;
        content: ' ';
        position: absolute;
        left: 10px;
        right: 10px;
        bottom: 0;
        height: 1px;
        background: rgba(white, .3);
        transition: right .35s ease-out; }
      &:hover {
        color: rgba(white, .7);
        &::after {
          right: 101%; } } } } }

@include r($hd) {
  .about-hero {
    &__content {
      padding-top: 320px; } }

  .about-content {
    .wysiwyg {
      p {
        font-size: 16px;
        line-height: 1.65; } }
    &__wrapper {
      padding-bottom: 30px; } }

  .about-navi {
    padding: 130px 50px 70px;
    &__list {
      a {
        font-size: 30px; } } } }

@include r($xl) {
  .about-hero {
    &__title {
      font-size: 30px;
      max-width: 780px;
      margin: 0 auto; } }

  .about-content {
    margin-top: -150px;
    &__wrapper {
      max-width: 810px;
      margin: 0 auto; }
    &__text {
      flex: 0 0 60%;
      max-width: 60%; }
    .wysiwyg {
      p {
        font-size: 14px;
        line-height: 1.75; }
      h3 {
        font-size: 14px; } } }

  .about-navi {
    padding: 120px 50px 70px;
    &__heading {
      font-size: 14px; }
    &__list {
      li {
        margin: 11px -10px 13px; }
      a {
        font-size: 24px; } } } }



@include r($lg) {
  .about-hero {
    &__title {
      font-size: 26px; } }

  .about-content {
    margin-top: -175px;
    &__wrapper {
      max-width: 100%;
      margin: 0;
      flex-direction: column;
      &::before {
        display: none; } }
    &__text {
      padding-left: 0;
      flex: 0 0 100%;
      max-width: 100%;
      padding-right: 0; }
    &__sidebar {
      padding-left: 0;
      padding-right: 0;
      margin-top: 12px;
      flex: 0 0 100%;
      max-width: 100%; } }

  .about-navi {
    padding: 60px 0;
    &__heading {
      font-size: 14px; }
    &__list {
      li {
        margin: 11px -10px 13px; }
      a {
        font-size: 24px; } } } }

@include r($md) {
  .about-hero {
    &__content {
      padding-top: 272px; }
    &__title {
      font-size: 18px;
      line-height: 1.45; } }

  .about-content {
    margin-top: -190px;
    .wysiwyg {
      p {
        font-size: 12px;
        line-height: 1.9; }
      h3 {
        font-size: 16px;
        text-transform: uppercase; } }
    &__sidebar {
      margin-top: 10px; } }


  .about-navi {
    padding: 45px 0 70px;
    &__heading {
      font-size: 14px;
      margin-bottom: 20px; }
    &__list {
      li {
        margin: 5px 0px 0px; }
      a {
        font-size: 18px; } } } }
