// buttons
button {
  -webkit-appearance: none;
  border: 0;
  box-shadow: none;
  cursor: pointer;
  &:focus {
    outline: none; } }

.btn {
  display: inline-block;
  position: relative;
  text-align: center;
  cursor: pointer;
  transition: all $transitionSpeed $ease;
  &:focus, &:active {
    outline: none; }
  &-primary {
    // please use paddings instead of line-height to control btn height
    background: transparent;
    border: 2px solid $colorPrimary;
    font-size: 14px;
    font-weight: 500;
    padding: 15px 25px;
    color: $fontColor;
    text-transform: uppercase;
    transition: .25s $ease;
    transform-origin: center bottom;
    box-shadow: none;
    overflow: hidden;
    will-change: box-shadow, transform;
    &::after {
      display: inline-block;
      content: ' ';
      position: absolute 0;
      z-index: 1;
      transform: translateY(100%);
      background: $colorPrimary;
      transition: transform .25s $ease; }
    span {
      position: relative;
      z-index: 2; }
    &:hover {
      color: white;
      &::after {
        transform: translateY(0); } } }
  &--block {
    display: block;
    width: 100%; }
  &--c-white {
    color: white; }
  &--modal {
    width: 100%;
    font-size: 12px;
    line-height: 1.25;
    padding: 13px 13px 12px;
    color: #fff;
    background: $colorGreen;
    text-transform: uppercase;
    &:hover {
      background: darken($colorGreen, 5); } } }

@include r($xl) {
  .btn {
    &-primary {
      padding: 12px 25px;
      font-size: 12px; } } }
