.scaler img [js-lazy] {
  will-change: opacity;
  backface-visibility: hidden;
}

.scaler.is-bg-hidden.no-bg-onload {
  background-image: none !important;
}

.scaler.is-loaded img, .scaler.is-loaded picture {
  opacity: 1;
}

img[js-lazy], picture[js-lazy] {
  opacity: 0;
  transition: opacity 0.25s ease-in-out, transform 0.4s ease-in-out;
}

img.is-loaded, picture.is-loaded {
  opacity: 1;
}

.reveal {
  position: relative;
  overflow: hidden;
}

.reveal::after {
  display: inline-block;
  content: ' ';
  position: absolute 0;
  background: white;
  z-index: 2;
  pointer-events: none;
  transition: transform 0.8s cubic-bezier(0.9, 0, 0.1, 1);
}

.reveal--black::after {
  background: #000;
}

.reveal.is-animated[data-direction="ltr"]::after {
  transform: translate(100%, 0);
}

.reveal.is-animated[data-direction="rtl"]::after {
  transform: translate(-100%, 0);
}

