.hamburger {
  size: 100px;
  background: $colorOrange;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  // transition: background .25s $ease
  font: inherit;
  color: inherit;
  text-transform: none;
  border: 0;
  overflow: visible;
  &:hover:not(.is-active) {
    // background-color: $colorPrimary
    .hamburger-inner {
      &::before {
        top: -11px; }
      &::after {
        bottom: -11px; } } } }

.hamburger-box {
  width: 24px;
  height: 24px;
  display: inline-block;
  position: relative; }

.hamburger-inner {
  display: block;
  top: 50%;
  width: 24px;
  height: 3px;
  margin-top: -2px;
  border-radius: 0px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
  &::before, &::after {
    width: 24px;
    height: 3px;
    border-radius: 0px;
    position: absolute;
    transition-property: transform;
    transition-duration: 0.15s;
    transition-timing-function: ease; }
  &::before, &::after {
    content: "";
    display: block; }
  &::before {
    top: -8px; }
  &::after {
    bottom: -8px; } }


.hamburger--squeeze {
  .hamburger-inner {
    transition-duration: 0.1s;
    transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
    &::before {
      transition: top 0.1s 0.14s ease, opacity 0.1s ease; }
    &::after {
      transition: bottom 0.1s 0.14s ease, transform 0.1s cubic-bezier(0.55, 0.055, 0.675, 0.19); } }
  &.is-active .hamburger-inner {
    transform: rotate(45deg);
    transition-delay: 0.14s;
    transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    &::before {
      top: 0;
      opacity: 0;
      transition: top 0.1s ease, opacity 0.1s 0.14s ease; }
    &::after {
      bottom: 0;
      transform: rotate(-90deg);
      transition: bottom 0.1s ease, transform 0.1s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1); } } }

@include r($hd) {
  .hamburger {
    size: 80px; } }
@include r($sm) {
  .hamburger {
    size: 48px;
    &:hover:not(.is-active) {
      .hamburger-inner {
        &::before {
          top: -8px; }
        &::after {
          bottom: -8px; } } } }

  .hamburger-box {
    width: 18px;
    height: 18px; }
  .hamburger-inner {
    width: 18px;
    height: 2px;
    &::before, &::after {
      width: 18px;
      height: 2px; }
    &::before {
      top: -6px; }
    &::after {
      bottom: -6px; } } }
