* {
  box-sizing: border-box;
  outline: none; }

a {
  color: inherit;
  text-decoration: none;
  &:hover, &:focus {
    text-decoration: none; } }

ul {
  list-style: none;
  li {
    display: inline-block; } }

body, html {
  background: white;
  color: $fontColor;
  font-family: $baseFont;
  // font-family: "Helvetica", -apple-system, 'BlinkMacSystemFont', 'Segoe UI', 'Roboto', 'Helvetica', 'Ubuntu', 'Cantarell', 'Arial', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol'
  font-size: 16px;
  min-width: 320px;
  max-width: 100%;
  margin: 0;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

::selection {
  color: white;
  background: $colorPrimary; }

.body-lock {
  height: 100%;
  overflow: hidden;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%; }

.body-lock {
  margin-bottom: 0 !important;
  .bc-container {
    display: none !important; }
  .footer {
    position: static !important; } }

.page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  &__content {
    // used for sticky footer (flexbox way)
    flex: 1 0 auto;
    display: flex;
    flex-direction: column; } }

.container {
  max-width: 1080px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 40px;
  padding-right: 40px;
  &--full {
    max-width: 100%; } }

@include r($hd) {
  .container {
    max-width: 1060px; } }

// +r($xl)
//   .container
//     max-width: 864px

@include r($sm) {
  .container {
    padding-left: 16px;
    padding-right: 16px; } }

.js-transition-overlay {
  position: fixed;
  top: 100%;
  left: 0;
  right: 0;
  height: 100vh;
  z-index: 7;
  background: $colorRed;
  will-change: transform;
  backface-visibility: hidden; }

// REMOVE FOR PRODUCTION !
.dev-bp-debug {
  background: black;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 9999;
  color: white;
  padding: 5px;
  font-size: 12px;
  border-bottom-left-radius: 5px; }

